.container{
    width: 100%;
    padding: 20px;
    display: flex;
}
.table{
    width: 50%;
}
.input-group{
    display: flex;
    flex-direction: column;
}
.input-group label{
    font-size: bold;
    margin-bottom: 5px;
}
.input-group input{
    height: 30px;
    width: 350px;
    margin-bottom:15px ;
}
.action{
    cursor: pointer;
    color: skyblue;
    text-decoration: underline;
}
button{
    min-width:100px;
    height: 30px;
    background-color: rgb(63, 220, 63);
    border-radius: 5px;
    border:none;
    cursor: pointer;
}
table{
    border-collapse: collapse;
}
table tr td,
table tr th
{
    border:solid 2px;
    width: 25%;
    height: 30px;
    text-align: center;
}
